<template>
    connecting to account...
</template>

<script>
    export default {
        name: 'connectView',
        components: {
        },
        created() {
            var myHeaders = new Headers();
            myHeaders.append("token", this.$route.query.token);

            var requestOptions = {
              method: 'GET',
              headers: myHeaders,
              redirect: 'follow'
            };

            fetch(localStorage.server + "/user/token/verify", requestOptions)
            .then(res => {
                res.json().then((ans) => {
                    if (ans["answer"] == "ok") {
                        localStorage.token = this.$route.query.token;
                        if (localStorage.path) {
                            this.$router.push({ name: localStorage.path });
                            localStorage.removeItem("path");
                            return;
                        }
                        this.$router.push({ name: 'Dashboard' });
                    }
                    else {
                        console.log("error");
                        this.$router.push({ name: 'Login' });
                    }
                })
            })
            .catch(error => console.log('error', error));
        }
    };
</script>
  
